import Vue from "vue"

const state = {
    // Item: {
    //   ID
    //   Course ID
    //   Name
    //   Category
    //   Description
    // }
    items: {},
    maxID: 1
}

const getters = {
    courses: (state) => {
        return Object.values(state.items)
    },
    getCourse: (state) => (courseId) => {
        return state.items[courseId]
    },
    getCourseSelectList: (state) => () => {
        return Object.keys(state.items).map(key => {
            return {
                value: key,
                label: state.items[key].name
            }
        })
    },
    rawData: (state) => {
        return state
    }
}

const mutations = {
    resetData(state, data) {
        state.items = data.items
        state.maxID = data.maxID
    },
    addCourse(state, course) {
        if (course.courseId) {
            const courseId = parseInt(course.courseId)
            if (courseId >= state.maxID) {
                state.maxID = courseId + 1
            }
        } else {
            course.courseId = state.maxID
            state.maxID += 1
        }
        Vue.set(state.items, course.courseId, course)
    },
    setCourse(state, course) {
        Vue.set(state.items, course.courseId, course)
    },
    deleteCourse(state, courseId) {
        Vue.delete(state.items, courseId)
    },
    destoryCourses(state) {
        state.items = {}
        state.maxID = 1
    }
}

const actions = {
    addCourse({ commit }, course) {
        return new Promise((resolve) => {
            commit('addCourse', course)
            resolve(course)
        })
    },
    editCourse({ commit, getters }, course) {
        return new Promise((resolve, reject) => {
            const {
                courseId
            } = course
            if (!courseId) {
                reject("ID不存在")
                return
            }
            if (!getters.getCourse(courseId)) {
                reject("課程不存在")
                return
            }
            commit('setCourse', course)
            resolve()
        })
    },
    deleteCourse({ commit }, courseId) {
        commit('deleteCourse', courseId)
    },
    destoryCourses({ commit }) {
        commit('destoryCourses')
    },
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}

