import Vue from "vue"

const state = {
    // Item: {
    //   ID
    //   Teacher ID
    //   Name
    //   Category
    //   Description
    // }
    items: {},
    maxID: 1
}

const getters = {
    teachers: (state) => {
        return Object.values(state.items)
    },
    getTeacher: (state) => (teacherId) => {
        return state.items[teacherId]
    },
    getTeacherSelectList: (state) => () => {
        return Object.keys(state.items).map(key => {
            return {
                value: key,
                label: state.items[key].name
            }
        })
    },
    rawData: (state) => {
        return state
    }
}

const mutations = {
    resetData(state, data) {
        state.items = data.items
        state.maxID = data.maxID
    },
    addTeacher(state, teacher) {
        if (teacher.teacherId) {
            const teacherId = parseInt(teacher.teacherId)
            if (teacherId >= state.maxID) {
                state.maxID = teacherId + 1
            }
        } else {
            teacher.teacherId = state.maxID
            state.maxID += 1
        }
        Vue.set(state.items, teacher.teacherId, teacher)
    },
    setTeacher(state, teacher) {
        Vue.set(state.items, teacher.teacherId, teacher)
    },
    deleteTeacher(state, teacherId) {
        Vue.delete(state.items, teacherId)
    },
    destoryTeachers(state) {
        state.items = {}
        state.maxID = 1
    }
}

const actions = {
    addTeacher({ commit }, teacher) {
        return new Promise((resolve) => {
            commit('addTeacher', teacher)
            resolve(teacher)
        })
    },
    editTeacher({ commit, getters }, teacher) {
        return new Promise((resolve, reject) => {
            const {
                teacherId
            } = teacher
            if (!teacherId) {
                reject("ID不存在")
                return
            }
            if (!getters.getTeacher(teacherId)) {
                reject("老師不存在")
                return
            }
            commit('setTeacher', teacher)
            resolve()
        })
    },
    deleteTeacher({ commit }, teacherId) {
        commit('deleteTeacher', teacherId)
    },
    destoryTeachers({ commit }) {
        commit('destoryTeachers')
    },
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}

