import Vue from "vue"

const state = {
    studentTeacherShips: {},
    teacherStudentShips: {}
}

const getters = {
    getTeacherAndCourseByStudentID: (state) => (studentId) => {
        return state.studentTeacherShips[studentId] || []
    },
    getStudentAndCourseByStudentID: (state) => (teacherId) => {
        return state.teacherStudentShips[teacherId] || []
    },
    rawData: (state) => {
        return state
    }
}

const mutations = {
    resetData(state, data) {
        state.studentTeacherShips = data.studentTeacherShips
        state.teacherStudentShips = data.teacherStudentShips
    },
    addToShip(state, { studentID, teacherID, courseID }) {
        if (!state.studentTeacherShips[studentID])
            Vue.set(state.studentTeacherShips, studentID, [])

        state.studentTeacherShips[studentID].push({
            teacherID,
            courseID
        })
        if (!state.teacherStudentShips[teacherID])
            Vue.set(state.teacherStudentShips, teacherID, [])
        state.teacherStudentShips[teacherID].push({
            studentID,
            courseID
        })
    },
    removeFromShip(state, { studentID, index }) {
        const {
            teacherID,
            courseID
        } = state.studentTeacherShips[studentID][index]
        const foundIndex = state.teacherStudentShips[teacherID].findIndex((ship) => {
            return ship.studentID === studentID && ship.courseID === courseID
        })
        if (foundIndex >= 0) {
            state.studentTeacherShips[studentID].splice(index, 1)
            state.teacherStudentShips[teacherID].splice(foundIndex, 1)
        }
    }
}

const actions = {
    addShip({ commit }, shipData) {
        const {
            studentID,
            teacherID,
            courseID
        } = shipData
        commit('addToShip', { studentID, teacherID, courseID })
    },
    removeShip({ commit }, { studentID, index }) {
        commit('removeFromShip', { studentID, index })
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}