<template>
	<el-main>
		<el-row type="flex">
			<el-button
				class="el-icon-plus"
				type="primary"
				@click="onNewClick"
				circle
			></el-button>
		</el-row>
		<el-row>
			<el-table :data="courses" stripe style="width: 100%">
				<el-table-column type="index" width="50"> </el-table-column>
				<el-table-column label="編號" sortable prop="courseId" width="180">
				</el-table-column>
				<el-table-column label="課程名稱" sortable prop="name" width="180">
				</el-table-column>
				<el-table-column align="right">
					<template slot="header" slot-scope="scope">
						<el-input
							v-model="search"
							@click="scope"
							size="mini"
							placeholder="請輸入關鍵字搜尋"
						>
						</el-input>
					</template>
					<template slot-scope="scope">
						<el-button
							class="el-icon-edit"
							type="primary"
							circle
							@click="handleEdit(scope.$index, scope.row)"
						></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-row>
		<el-row>
			<el-dialog :visible.sync="dialogVisible" :before-close="onDialogClose">
				<el-form ref="form" :model="courseData">
					<el-form-item label="編號">
						{{ courseData.courseId }}
					</el-form-item>

					<el-form-item label="課程名稱">
						<el-input v-model="courseData.name"></el-input>
					</el-form-item>
				</el-form>

				<!--<el-popconfirm
					v-if="editMode"
					title="確定要刪除嗎?"
					@confirm="handleDelete(courseData.courseId)"
				>
					<el-button
						size="mini"
						type="danger"
						class="el-icon-delete"
						slot="reference"
						circle
					></el-button>
				</el-popconfirm>-->

				<div slot="footer" class="dialog-fooger">
					<el-button @click="onDialogClose">關閉</el-button>
					<el-button type="success" @click="onDialogConfirm">{{
						this.editMode ? "修改" : "新增"
					}}</el-button>
				</div>
			</el-dialog>
		</el-row>
	</el-main>
</template>

<script>
import { mapActions, mapGetters } from "vuex"

export default {
	data() {
		return {
			dialogVisible: false,
			editMode: false,
			courseData: {},
			search: ""
		}
	},
	methods: {
		handleEdit(index, row) {
			const course = this.getCourse(row.courseId)
			if (course) {
				this.courseData = { ...course }
				this.editMode = true
				this.dialogVisible = true
			}
		},
		handleDelete(courseId) {
			this.deleteCourse(courseId)
			this.editMode = false
			this.dialogVisible = false
			this.clearFormData()
		},
		onDialogClose() {
			this.dialogVisible = false
			this.clearFormData()
		},
		onNewClick() {
			this.editMode = false
			this.dialogVisible = true
		},
		onDestoryClick() {
			this.$store.dispatch("course/destoryCourses")
		},
		onDialogConfirm() {
			if (this.editMode) {
				this.editCourse(this.courseData)
					.then(() => {
						this.$message({
							message: `${this.courseData.name} 修改成功`,
							type: "success"
						})
						this.dialogVisible = false
						this.clearFormData()
					})
					.catch(error => {
						this.$message({
							message: `修改失敗: ${error}`,
							type: "error"
						})
					})
			} else {
				this.addCourse(this.courseData)
					.then(() => {
						this.$message({
							message: `${this.courseData.name} 新增成功`,
							type: "success"
						})
						this.clearFormData()
					})
					.catch(error => {
						this.$message({
							message: `新增失敗: ${error}`,
							type: "error"
						})
					})
			}
		},
		clearFormData() {
			this.courseData = {}
		},
		...mapActions("course", ["addCourse", "editCourse", "deleteCourse"])
	},
	computed: {
		...mapGetters("course", ["courses", "getCourse"])
	}
}
</script>