import Vue from "vue"

const state = {
    // items: {
    //     studentID: {
    //         ID
    //         Student ID
    //         Name
    //         Category
    //         Description
    //         Ships: [
    //             {
    //                 TeacherID
    //                 CourseID
    //             }
    //         ]
    //     }
    // }
    // teacherStudentShips: {
    //     teacherID: [
    //         studentID
    //     ]
    // }
    items: {}
}

const getters = {
    students: (state) => {
        return Object.values(state.items)
    },
    getStudent: (state) => (studentId) => {
        return state.items[studentId]
    },
    rawData: (state) => {
        return state
    }
}

const mutations = {
    resetData(state, data) {
        state.items = data.items
    },
    setStudent(state, student) {
        // state.items[student.studentId] = student
        Vue.set(state.items, student.studentId, student)
    },
    deleteStudent(state, studentId) {
        Vue.delete(state.items, studentId)
    },
    destoryStudents(state) {
        state.items = {}
    }
}

const actions = {
    addStudent({ commit, getters }, student) {
        return new Promise((resolve, reject) => {
            const {
                studentId
            } = student
            if (!studentId) {
                reject("請輸入學號")
                return
            }
            if (getters.getStudent(studentId)) {
                reject("學號已存在")
                return
            }
            commit('setStudent', student)
            resolve()
        })
    },
    editStudent({ commit, getters }, student) {
        return new Promise((resolve, reject) => {
            const {
                studentId
            } = student
            if (!studentId) {
                reject("請輸入學號")
                return
            }
            if (!getters.getStudent(studentId)) {
                reject("學生不存在")
                return
            }
            commit('setStudent', student)
            resolve()
        })
    },
    deleteStudent({ commit }, studentId) {
        commit('deleteStudent', studentId)
    },
    destoryStudents({ commit }) {
        commit('destoryStudents')
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}

