<template>
	<div>
		<el-alert
			title="本頁修改的內容僅供本次列印使用，並不會儲存修改內容。"
			type="warning"
		>
		</el-alert>
		<el-form>
			<el-form-item label="學期">
				<el-input v-model="semester"></el-input>
			</el-form-item>
			<el-form-item label="描述">
				<el-tiptap
					v-model="footer"
					:extensions="extensions"
					lang="zh_tw"
				></el-tiptap>
			</el-form-item>
		</el-form>
		<el-button @click="onPrintClick" type="success">列印</el-button>
		<el-divider></el-divider>
		<div id="printPage">
			<div class="container">
				<div class="row mb-5">
					<img
						src="../assets/ccut.jpg"
						alt="ccut"
						width="60"
						height="60"
						class="float:right"
					/>
					<h1 class="col" align="center">
						{{ semester }}學期 班級身心障礙學生通知單
					</h1>
				</div>
				<div class="row"></div>
				<div class="row mb-3">
					<h3 class="">
						◎ 任課教師： {{ teacher.name }} 老師({{ teacher.department }})
					</h3>
				</div>
				<div class="row">
					<p>親愛的老師您好：</p>
					<p>
						為了幫助貴班身心障礙學生在校學習順利，並讓您瞭解本學期在您班級裡的特殊學生，所以
						我們統整了班上相關學生名單，以便您提供進一步的關懷與協助。因每位學生的障礙程度不
						同皆需要個別化的輔導計畫。在此資源教室僅提供概況需求供師長們參考。
					</p>

					<p>
						<u>
							此表只作為提供老師了解班上特殊生名單，每個學生狀況不盡相同，需老師多一點協助與關懷。
							因本表涉及學生個人隱私，如未經同意請勿任意將其特殊生身分公開，避免將學生標籤化，也請您妥善保管資
							料（閱畢亦可銷毀），感謝您的配合。
						</u>
					</p>
				</div>
				<div
					id="students"
					v-for="(course, key) in studentsBelongsCourse()"
					:key="key"
				>
					<hr />
					<div class="row mb-2">
						<h2>◎ 課程: {{ course.name }}</h2>
					</div>
					<div class="row table-responsive">
						<table class="table table-bordered">
							<thead>
								<tr>
									<th style="width: 100px">系級</th>
									<th style="width: 100px">學號</th>
									<th style="width: 100px">姓名</th>
									<th style="width: 140px">障礙類別/程度</th>
									<th>可能需要特別注意的事項</th>
								</tr>
							</thead>
							<tbody>
								<tr v-for="(student, index) in course.students" :key="index">
									<td>{{ student.department }}</td>
									<td>{{ student.studentId }}</td>
									<td>{{ student.name }}</td>
									<td>{{ student.category }}</td>
									<td><div v-html="student.description"></div></td>
								</tr>
							</tbody>
						</table>
					</div>
				</div>
				<div v-html="footer"></div>
			</div>
		</div>
	</div>
</template>

<script>
import {
	Doc,
	Text,
	Paragraph,
	Heading,
	Bold,
	Underline,
	Italic,
	Strike,
	Code,
	Link,
	Blockquote,
	ListItem,
	BulletList,
	OrderedList,
	TodoItem,
	TodoList,
	TextAlign,
	Indent,
	HardBreak,
	HorizontalRule,
	History,
	ElementTiptap
} from "element-tiptap"

export default {
	components: {
		"el-tiptap": ElementTiptap
	},
	methods: {
		async onPrintClick() {
			await this.$htmlToPaper("printPage")
		},
		getSemesterString() {
			const today = new Date()
			return `中州科技大學${today.getFullYear() - 1911}-${
				today.getMonth() > 6 ? 1 : 2
			}`
		},
		studentsBelongsCourse() {
			return this.students.reduce((prev, curr) => {
				if (!prev[curr.courseId])
					prev[curr.courseId] = { name: curr.courseName, students: [] }
				prev[curr.courseId].students.push(curr)
				return prev
			}, {})
		}
	},
	props: {
		teacher: Object,
		students: Array
	},
	data() {
		return {
			semester: this.getSemesterString(),
			footer:
				'<p style="text-align: right;">資源教室位於工學館一樓保健與諮商中心</p> \
<p style="text-align: right;">服務時間上午8：00至下午5：00</p> \
<p style="text-align: right;">聯絡電話：04-8359000&nbsp;轉&nbsp;1354</p> \
<p style="text-align: right;">E-mail：rc1352@gm.ccut.edu.tw</p> \
<p style="text-align: right;">資源教室輔導老師 敬上</p>',
			extensions: [
				new Doc(),
				new Text(),
				new Paragraph(),
				new Heading({ level: 5 }),
				new Bold({ bubble: true }),
				new Underline({ bubble: true }),
				new Italic({ bubble: true }),
				new Strike({ bubble: true }),
				new Code(),
				new Link({ bubble: true }),
				new Blockquote(),
				new TextAlign(),
				new ListItem(),
				new BulletList({ bubble: true }),
				new OrderedList({ bubble: true }),
				new TodoItem(),
				new TodoList(),
				new Indent(),
				new HardBreak(),
				new HorizontalRule({ bubble: true }),
				new History()
			]
		}
	}
}
</script>
<style scoped>
@import "https://cdn.jsdelivr.net/npm/bootstrap@4.6.0/dist/css/bootstrap.min.css";
</style>