import Vue from 'vue'
import Vuex from 'vuex'
import student from './modules/student'
import teacher from './modules/teacher'
import course from './modules/course'
import ships from './modules/ships'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

const actions = {
    loadData({ commit }, importData) {
        return new Promise((resolve, reject) => {
            try {
                const {
                    students,
                    teachers,
                    courses,
                    ships
                } = importData
                commit('student/resetData', students)
                commit('teacher/resetData', teachers)
                commit('course/resetData', courses)
                commit('ships/resetData', ships)
                console.log(importData)
                resolve()
            } catch {
                reject()
            }
        })
    },
    saveData({ rootGetters }) {
        const data = {
            students: rootGetters['student/rawData'],
            teachers: rootGetters['teacher/rawData'],
            courses: rootGetters['course/rawData'],
            ships: rootGetters['ships/rawData'],
        }
        const blob = new Blob([JSON.stringify(data)], { type: 'text/plain' })
        const e = document.createEvent('MouseEvent')
        let a = document.createElement('a')
        const today = new Date()
        const fileName = `isp_${today.getFullYear()}${today.getMonth()}${today.getDate()}_${today.getHours()}${today.getMinutes()}${today.getSeconds()}.json`
        a.download = fileName
        a.href = window.URL.createObjectURL(blob)
        a.dataset.downloadurl = ['text/json', a.download, a.href].join(':')
        e.initEvent('click', true, false)
        a.dispatchEvent(e)
    },
    reset() {

    }
}

const dataState = createPersistedState({
    paths: ['student', 'teacher', 'course', 'ships']
})

export default new Vuex.Store({
    actions,
    modules: {
        student,
        teacher,
        course,
        ships
    },
    // plugins: [createPersistedState()],
    plugins: [dataState],
    strict: debug
})