<template>
	<el-main>
		<el-row type="flex">
			<el-tooltip content="新增" placement="bottom">
				<el-button
					class="el-icon-plus"
					type="primary"
					@click="onNewClick"
					circle
				></el-button>
			</el-tooltip>
			<!--<el-button
				class="el-icon-delete-solid"
				type="danger"
				@click="onDestoryClick"
				circle
			></el-button>-->
			<el-tooltip content="備份" placement="bottom">
				<el-button
					class="el-icon-download"
					type="info"
					circle
					@click="onBackupClick"
				>
				</el-button>
			</el-tooltip>
			<el-tooltip content="匯入" placement="bottom">
				<el-upload action="." :on-change="onLoadFile" accept="application/JSON">
					<el-button class="el-icon-upload2" type="info" circle> </el-button>
				</el-upload>
			</el-tooltip>
		</el-row>
		<el-row>
			<el-table :data="studentsFilter" stripe style="width: 100%">
				<el-table-column type="expand">
					<template slot-scope="props">
						<div v-html="props.row.description"></div>
					</template>
				</el-table-column>
				<el-table-column type="index" width="50"> </el-table-column>
				<el-table-column label="學號" sortable prop="studentId" width="180">
				</el-table-column>
				<el-table-column label="姓名" sortable prop="name" width="180">
				</el-table-column>
				<el-table-column
					label="障礙類別/程度"
					sortable
					prop="category"
					width="180"
				>
				</el-table-column>
				<el-table-column label="系級" sortable prop="department" width="180">
				</el-table-column>
				<!-- <el-table-column label="課程" sortable width="180">
					<template slot-scope="scope">
						{{ scope.row.course ? getCourse(scope.row.course).name : "" }}
					</template>
				</el-table-column> -->
				<!-- <el-table-column label="老師" width="300">
					<template slot-scope="scope">
						<el-tag
							type="primary"
							v-for="(teacherID, index) in scope.row.teachers"
							:key="index"
							>{{ teacherID }}</el-tag
						>
					</template>
				</el-table-column> -->
				<!--<el-table-column label="描述" prop="description" width="180">
				</el-table-column>-->
				<el-table-column align="right">
					<template slot="header" slot-scope="scope">
						<el-input
							v-model="search"
							@click="scope"
							size="mini"
							placeholder="請輸入關鍵字搜尋"
						>
						</el-input>
					</template>
					<template slot-scope="scope">
						<el-button
							class="el-icon-edit"
							type="primary"
							circle
							@click="handleEdit(scope.$index, scope.row)"
						></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-row>
		<el-row>
			<el-dialog :visible.sync="dialogVisible" :before-close="onDialogClose">
				<el-form ref="form" :model="studentData">
					<el-form-item label="學號(不可重覆)">
						<el-input v-model="studentData.studentId"></el-input>
					</el-form-item>

					<el-form-item label="姓名">
						<el-input v-model="studentData.name"></el-input>
					</el-form-item>

					<el-form-item label="障礙類別/程度">
						<el-input v-model="studentData.category"></el-input>
					</el-form-item>

					<el-form-item label="系級">
						<el-input v-model="studentData.department"></el-input>
					</el-form-item>

					<el-form-item label="描述">
						<el-tiptap
							v-model="studentData.description"
							:extensions="extensions"
							lang="zh_tw"
						></el-tiptap>
					</el-form-item>
					<el-form-item>
						<el-popconfirm
							v-if="editMode"
							title="確定要刪除嗎?"
							confirm-button-type="danger"
							@confirm="handleDelete(studentData.studentId)"
						>
							<el-button type="danger" slot="reference">刪除</el-button>
						</el-popconfirm>
						<el-button @click="onDialogClose">關閉</el-button>
						<el-button type="success" @click="onDialogConfirm">{{
							this.editMode ? "儲存" : "新增"
						}}</el-button>
					</el-form-item>
					<div v-if="this.editMode">
						<el-divider>以下設定不需按儲存</el-divider>

						<el-form-item label="老師">
							<el-col :span="10">
								<el-select
									v-model="selectedTeacher"
									filterable
									placeholder="請選擇課程"
								>
									<el-option
										v-for="teacher in getTeacherSelectList()"
										:key="teacher.value"
										:label="teacher.label"
										:value="teacher.value"
									>
									</el-option>
								</el-select>
							</el-col>
							<el-col :span="1"> ← </el-col>
							<el-col :span="6">
								<el-input v-model="newTeacher"></el-input>
							</el-col>
							<el-col :span="4">
								<el-button
									type="primary"
									class="el-icon-plus"
									circle
									@click="onAddTeacherClick"
								></el-button>
							</el-col>
						</el-form-item>

						<el-form-item label="課程">
							<el-col :span="10">
								<el-select
									v-model="selectedCourse"
									filterable
									placeholder="請選擇課程"
								>
									<el-option
										v-for="course in getCourseSelectList()"
										:key="course.value"
										:label="course.label"
										:value="course.value"
									>
									</el-option>
								</el-select>
							</el-col>
							<el-col :span="1"> ← </el-col>
							<el-col :span="6">
								<el-input v-model="newCourse"></el-input>
							</el-col>
							<el-col :span="4">
								<el-button
									type="primary"
									class="el-icon-plus"
									circle
									@click="onAddCourseClick"
								></el-button>
							</el-col>
						</el-form-item>

						<el-form-item>
							<el-button
								type="primary"
								class="el-icon-plus"
								@click="onNewShipClick"
								>新增老師與課程</el-button
							>
						</el-form-item>

						<el-form-item>
							<el-table
								stripe
								:data="getTeacherAndCourseByStudentID(studentData.studentId)"
							>
								<el-table-column type="index"></el-table-column>
								<el-table-column label="老師">
									<template slot-scope="scope">
										{{ teacherName(scope.row.teacherID) }}
									</template>
								</el-table-column>
								<el-table-column label="課程">
									<template slot-scope="scope">
										{{ courseName(scope.row.courseID) }}
									</template>
								</el-table-column>
								<el-table-column>
									<template slot-scope="scope">
										<el-button
											class="el-icon-delete"
											type="danger"
											circle
											@click="onShipDeleteClick(scope.$index, scope.row)"
										>
										</el-button>
									</template>
								</el-table-column>
							</el-table>
						</el-form-item>
					</div>
				</el-form>

				<!--<div slot="footer" class="dialog-fooger">
					<el-popconfirm
						v-if="editMode"
						title="確定要刪除嗎?"
						confirm-button-type="danger"
						@confirm="handleDelete(studentData.studentId)"
					>
						<el-button type="danger" slot="reference">刪除</el-button>
					</el-popconfirm>
					<el-button @click="onDialogClose">關閉</el-button>
					<el-button type="success" @click="onDialogConfirm">{{
						this.editMode ? "修改" : "新增"
					}}</el-button>
				</div> -->
			</el-dialog>
		</el-row>
	</el-main>
</template>

<script>
import "element-tiptap/lib/index.css"
import { mapActions, mapGetters } from "vuex"
import {
	Doc,
	Text,
	Paragraph,
	Heading,
	Bold,
	Underline,
	Italic,
	Strike,
	Code,
	Link,
	Blockquote,
	ListItem,
	BulletList,
	OrderedList,
	TodoItem,
	TodoList,
	TextAlign,
	Indent,
	HardBreak,
	HorizontalRule,
	History,
	ElementTiptap
} from "element-tiptap"

export default {
	components: {
		"el-tiptap": ElementTiptap
	},
	data() {
		return {
			dialogVisible: false,
			editMode: false,
			studentData: {},
			search: "",
			selectedTeacher: null,
			selectedCourse: null,
			newTeacher: null,
			newCourse: null,
			description: null,
			extensions: [
				new Doc(),
				new Text(),
				new Paragraph(),
				new Heading({ level: 5 }),
				new Bold({ bubble: true }),
				new Underline({ bubble: true }),
				new Italic({ bubble: true }),
				new Strike({ bubble: true }),
				new Code(),
				new Link({ bubble: true }),
				new Blockquote(),
				new TextAlign(),
				new ListItem(),
				new BulletList({ bubble: true }),
				new OrderedList({ bubble: true }),
				new TodoItem(),
				new TodoList(),
				new Indent(),
				new HardBreak(),
				new HorizontalRule({ bubble: true }),
				new History()
			]
		}
	},
	methods: {
		handleEdit(index, row) {
			const student = this.getStudent(row.studentId)
			if (student) {
				this.studentData = { ...student }
				this.editMode = true
				this.dialogVisible = true
			}
		},
		handleDelete(studentId) {
			this.deleteStudent(studentId)
			this.editMode = false
			this.dialogVisible = false
			this.clearFormData()
		},
		onDialogClose() {
			this.dialogVisible = false
			this.clearFormData()
		},
		onNewClick() {
			this.editMode = false
			this.dialogVisible = true
		},
		onDestoryClick() {
			// this.$store.dispatch("student/destoryStudents")
			this.reset()
		},
		onDialogConfirm() {
			if (this.editMode) {
				this.editStudent(this.studentData)
					.then(() => {
						this.$message({
							message: `${this.studentData.studentId} 修改成功`,
							type: "success"
						})
						this.dialogVisible = false
						this.clearFormData()
					})
					.catch(error => {
						this.$message({
							message: `修改失敗: ${error}`,
							type: "error"
						})
					})
			} else {
				this.addStudent(this.studentData)
					.then(() => {
						this.$message({
							message: `${this.studentData.studentId} 新增成功`,
							type: "success"
						})
						this.clearFormData()
					})
					.catch(error => {
						this.$message({
							message: `新增失敗: ${error}`,
							type: "error"
						})
					})
			}
		},
		clearFormData() {
			this.studentData = {}
		},
		onNewShipClick() {
			if (!this.selectedTeacher && !this.selectedCourse) {
				this.$message({
					message: "請選擇老師與課程",
					type: "error"
				})
				return
			}
			this.addShip({
				studentID: this.studentData.studentId,
				teacherID: this.selectedTeacher,
				courseID: this.selectedCourse
			})
		},
		onShipDeleteClick(index) {
			this.removeShip({
				studentID: this.studentData.studentId,
				index
			})
		},
		onAddTeacherClick() {
			if (!this.newTeacher) {
				return
			}
			const teacherData = { name: this.newTeacher }
			this.addTeacher(teacherData)
				.then(teacher => {
					this.$message({
						message: `${teacher.name} 新增成功`,
						type: "success"
					})
					this.selectedTeacher = String(teacher.teacherId)
					this.newTeacher = null
				})
				.catch(error => {
					this.$message({
						message: `新增失敗: ${error}`,
						type: "error"
					})
				})
		},
		onAddCourseClick() {
			if (!this.newCourse) {
				return
			}
			const courseData = { name: this.newCourse }
			this.addCourse(courseData)
				.then(course => {
					this.$message({
						message: `${course.name} 新增成功`,
						type: "success"
					})
					this.selectedCourse = String(course.courseId)
					this.newCourse = null
				})
				.catch(error => {
					this.$message({
						message: `新增失敗: ${error}`,
						type: "error"
					})
				})
		},
		onBackupClick() {
			this.saveData()
		},
		onLoadFile(file) {
			const fr = new FileReader()
			fr.onload = async e => {
				try {
					let document = JSON.parse(e.target.result)
					this.loadData(document)
						.then(() => {
							this.$message({
								message: `匯入成功}`,
								type: "success"
							})
						})
						.catch(err => {
							this.$message({
								message: `匯入失敗: ${err.message}`,
								type: "error"
							})
						})
				} catch (err) {
					this.$message({
						message: `匯入失敗: ${err.message}`,
						type: "error"
					})
				}
			}
			fr.readAsText(file.raw)
		},
		teacherName(teacherID) {
			return this.getTeacher(teacherID)?.name || "已被刪除"
		},
		courseName(courseID) {
			return this.getCourse(courseID)?.name || "已被刪除"
		},
		...mapActions("student", ["addStudent", "editStudent", "deleteStudent"]),
		...mapActions("teacher", ["addTeacher"]),
		...mapActions("course", ["addCourse"]),
		...mapActions("ships", ["addShip", "removeShip"]),
		...mapActions(["reset", "saveData", "loadData"])
	},
	computed: {
		studentsFilter() {
			return this.students.filter(
				data =>
					!this.search ||
					data.name?.includes(this.search) ||
					data.studentId.includes(this.search)
			)
		},
		...mapGetters("student", ["students", "getStudent"]),
		...mapGetters("course", ["getCourseSelectList", "getCourse"]),
		...mapGetters("teacher", ["getTeacherSelectList", "getTeacher"]),
		...mapGetters("ships", ["getTeacherAndCourseByStudentID"])
	}
}
</script>
<style scoped>
@import "~vue-wysiwyg/dist/vueWysiwyg.css";
</style>