<template>
	<el-main>
		<el-row type="flex">
			<el-button
				class="el-icon-plus"
				type="primary"
				@click="onNewClick"
				circle
			></el-button>
		</el-row>
		<el-row>
			<el-table :data="teachersFilter" stripe style="width: 100%">
				<el-table-column type="expand">
					<template slot-scope="props">
						<el-table
							:data="getStudentAndCourseByStudentID(props.row.teacherId)"
							stripe
							style="width: 100%"
						>
							<el-table-column label="學生" width="200">
								<template slot-scope="scope">
									{{ getStudentName(scope.row.studentID) }}
								</template>
							</el-table-column>
							<el-table-column label="課程" width="200">
								<template slot-scope="scope">
									{{ getCourseName(scope.row.courseID) }}
								</template>
							</el-table-column>
						</el-table>
					</template>
				</el-table-column>
				<el-table-column type="index" width="50"> </el-table-column>
				<el-table-column label="編號" sortable prop="teacherId" width="180">
				</el-table-column>
				<el-table-column label="姓名" sortable prop="name" width="180">
				</el-table-column>
				<el-table-column label="科系" sortable prop="department" width="180">
				</el-table-column>
				<el-table-column label="學生數" sortable width="180">
					<template slot-scope="props">
						{{ getStudentAndCourseByStudentID(props.row.teacherId).length }}
					</template>
				</el-table-column>
				<el-table-column align="right">
					<template slot="header" slot-scope="scope">
						<el-input
							v-model="search"
							@click="scope"
							size="mini"
							placeholder="請輸入關鍵字搜尋"
						>
						</el-input>
					</template>
					<template slot-scope="scope">
						<el-button
							class="el-icon-edit"
							type="primary"
							circle
							@click="handleEdit(scope.$index, scope.row)"
						></el-button>
						<el-button
							class="el-icon-printer"
							type="info"
							circle
							@click="onPrintClick(scope.$index, scope.row)"
						>
						</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-row>
		<el-row>
			<el-dialog :visible.sync="dialogVisible" :before-close="onDialogClose">
				<el-form ref="form" :model="teacherData">
					<el-form-item label="編號">
						{{ teacherData.teacherId }}
					</el-form-item>

					<el-form-item label="姓名">
						<el-input v-model="teacherData.name"></el-input>
					</el-form-item>

					<el-form-item label="科系">
						<el-input v-model="teacherData.department"></el-input>
					</el-form-item>
				</el-form>

				<el-popconfirm
					v-if="editMode"
					title="確定要刪除嗎?"
					@confirm="handleDelete(teacherData.teacherId)"
				>
					<el-button
						size="mini"
						type="danger"
						class="el-icon-delete"
						slot="reference"
						circle
					></el-button>
				</el-popconfirm>

				<div slot="footer" class="dialog-fooger">
					<el-button @click="onDialogClose">關閉</el-button>
					<el-button type="success" @click="onDialogConfirm">{{
						this.editMode ? "儲存" : "新增"
					}}</el-button>
				</div>
			</el-dialog>
		</el-row>
		<el-row>
			<el-dialog width="80%" :visible.sync="printVisible">
				<print-page
					:teacher="teacherData"
					:students="listStudents"
					:ships="getStudentAndCourseByStudentID(teacherData.teacherId)"
				></print-page>
			</el-dialog>
		</el-row>
	</el-main>
</template>

<script>
import { mapActions, mapGetters } from "vuex"
import PrintPage from "./PrintPage.vue"

export default {
	components: {
		PrintPage
	},
	data() {
		return {
			dialogVisible: false,
			editMode: false,
			teacherData: {},
			search: "",
			printVisible: false
		}
	},
	methods: {
		handleEdit(index, row) {
			const teacher = this.getTeacher(row.teacherId)
			if (teacher) {
				this.teacherData = { ...teacher }
				this.editMode = true
				this.dialogVisible = true
			}
		},
		handleDelete(teacherId) {
			this.deleteTeacher(teacherId)
			this.editMode = false
			this.dialogVisible = false
			this.clearFormData()
		},
		onDialogClose() {
			this.dialogVisible = false
			this.clearFormData()
		},
		onNewClick() {
			this.editMode = false
			this.dialogVisible = true
		},
		onDestoryClick() {
			this.$store.dispatch("teacher/destoryTeachers")
		},
		onDialogConfirm() {
			if (this.editMode) {
				this.editTeacher(this.teacherData)
					.then(() => {
						this.$message({
							message: `${this.teacherData.name} 修改成功`,
							type: "success"
						})
						this.dialogVisible = false
						this.clearFormData()
					})
					.catch(error => {
						this.$message({
							message: `修改失敗: ${error}`,
							type: "error"
						})
					})
			} else {
				this.addTeacher(this.teacherData)
					.then(() => {
						this.$message({
							message: `${this.teacherData.name} 新增成功`,
							type: "success"
						})
						this.clearFormData()
					})
					.catch(error => {
						this.$message({
							message: `新增失敗: ${error}`,
							type: "error"
						})
					})
			}
		},
		getStudentName(studentID) {
			return this.getStudent(studentID)?.name
		},
		getCourseName(courseID) {
			return this.getCourse(courseID)?.name
		},
		async onPrintClick(index, row) {
			const teacher = this.getTeacher(row.teacherId)
			if (teacher) {
				this.teacherData = { ...teacher }
			}
			this.printVisible = true
			// await this.$htmlToPaper("printPage")
		},
		clearFormData() {
			this.teacherData = {}
		},
		...mapActions("teacher", ["addTeacher", "editTeacher", "deleteTeacher"])
	},
	computed: {
		teachersFilter() {
			return this.teachers.filter(
				data => !this.search || data.name?.includes(this.search)
			)
		},
		listStudents() {
			return this.getStudentAndCourseByStudentID(
				this.teacherData.teacherId
			).map(ship => {
				const student = this.getStudent(ship.studentID)
				return {
					...student,
					courseName: this.getCourse(ship.courseID)?.name,
					courseId: ship.courseID || 0
				}
			})
		},
		...mapGetters("teacher", ["teachers", "getTeacher"]),
		...mapGetters("student", ["getStudent"]),
		...mapGetters("course", ["getCourse"]),
		...mapGetters("ships", ["getStudentAndCourseByStudentID"])
	}
}
</script>